// Ê®°Êãü requestAnimationFrame
Object.defineProperty(window, 'requestAnimationFrame', {
  value: (callback) => {
    return setTimeout(callback, 16)
  }
})

Object.defineProperty(window, 'cancelAnimationFrame', {
  value: (id) => {
    clearTimeout(id)
  }
})

