global.testHelpers = {
  wait: (ms) => new Promise(resolve => setTimeout(resolve, ms)),
  createEvent: (type, data = {}) => {
    return new Event(type, data)
  },
  createError: (message, code = 'UNKNOWN') => {
    const error = new Error(message)
    Object.assign(error, { code })
    return error
  },
  clearAllMocks: () => {
    jest.clearAllMocks()
  },
  resetAllMocks: () => {
    jest.resetAllMocks()
  }
}

